import {Component, EventEmitter, OnInit, Output} from '@angular/core';

@Component({
  selector: 'search-box',
  template: `
    <div class="inputgroup" #rootDiv>
      <input
        id="input"
        role="combobox"
        name="searchbox"
        [(ngModel)]="selectedOption"
        type="text" class="input-control"
        (focus)="focusHandle($event)"
        (blur)="blurHandle($event)"
        [attr.placeholder]="'search'"/>
      <span class="drodown-caret-down" (click)="iconHandle($event)" style="cursor: pointer">
      <i class="fa fa-search"></i>
      </span>
      <span #dropdownitems  class="dropdown"  [ngStyle]="dropdownstyle">
          <ul class="dropdown-list">
            <li (mousedown)="liClickHandle(item)"  role="option"  class="list-items" *ngFor="let item of dropDownItemList">{{item.key}}</li>
          </ul>
        </span>
    </div>
  <br>

  `
})

export class SearchBoxComponent implements OnInit {
  dropdownstyle: any;
  showDropdown = false;
  dropDownItemList: any[] = [];
  selectedOption = '';
  @Output() onSelect: EventEmitter<any> =  new EventEmitter<any>();
  constructor() {
    this.dropDownItemList = [
      {
        'key': 'Customer ID',
        'id': 1
      },
      {
        'key': 'Mobile Number',
        'id': 2
      },
      {
        'key': 'Advanced Search',
        'id': 3
      }
    ];
  }

  ngOnInit() {
    this.showList(this.showDropdown);
  }

  focusHandle(event: any) {
    this.showList(true);
  }
  iconHandle(event: any) {
    this.showDropdown = !this.showDropdown;
    this.showList(this.showDropdown);
  }
  blurHandle(event: any) {
    this.showList(false);
  }

  liClickHandle(item: any) {
   this.selectedOption = item.key;
    this.showList(false);
    this.onSelect.emit(item);
  }

  showList(flag: boolean) {
    this.showDropdown = flag;
    if (this.showDropdown) {
      this.dropdownstyle = {visibility: 'visible'};
    } else {
      this.dropdownstyle = {visibility: 'hidden'};
    }
  }
}
