import {Component, OnInit, ViewChild} from '@angular/core';
import {GridConfig, AmexioGridLayoutService, GridConstants} from 'amexio-ng-extensions';
import {HttpClient} from '@angular/common/http';
import {CustomerDetails} from '../../../../rina-lib/src/lib/model/customer.details.model';
import {DynamicTabComponent} from './Dyanmictab.component';
import {VehicleDetails} from '../../../../rina-lib/src/lib/model/vehicel.details.model';

@Component({
  selector: 'rina-home',
  templateUrl: 'rina-home.component.html'
})

export class RinaHomeComponent implements OnInit {
  rinaHomeDesktopLayout: GridConfig;
  customerDetails: CustomerDetails;
  vehicleDetails: VehicleDetails;
  serviceList: any[] = [];
  @ViewChild('tab') tabRef: any;
  constructor(private _httpClient: HttpClient, private _gridlayoutService: AmexioGridLayoutService) {
    this.customerDetails = new CustomerDetails();
    this.createLayouts();
    this._gridlayoutService.createLayout(this.rinaHomeDesktopLayout);
  }

  ngOnInit() {
    this.getServiceList();
    this.getCustomerDetails();
    this.getVehicleDetails();

  }


  getServiceList() {
    this._httpClient.get('assets/serviceList.json').subscribe(
      (res: any) => {
        this.serviceList = res.data;
      }
    );
  }

  getCustomerDetails() {
    this._httpClient.get('assets/customerDetails.json').subscribe(
      (res: any) => {
        this.customerDetails = res.data;
      }
    );
  }
  getVehicleDetails() {
    this._httpClient.get('assets/vehicleDetails.json').subscribe(
      (res: any) => {
        this.vehicleDetails = res.data;
      }
    );
  }

  createLayouts() {
    this.rinaHomeDesktopLayout = new GridConfig('rinahomepage', GridConstants.Desktop)
      .addlayout(['logoposition', 'topslot2', 'topslot3', 'topslot4', 'guages'])
      .addlayout(['rinasidemenu', 'rinasidecenter', 'rinasidecenter', 'rinasidecenter', 'rinasidesearch']);

  }

  addServiceBookingTab(event: any) {
    if (!this.tabRef.setActiveTab('SERVICE BOOKING') && event.serviceName === 'SERVICE BOOKING') {
      this.tabRef.closeAllTabs();
      const cmp = this.tabRef.addDynamicTab('SERVICE BOOKING', ' ', false , DynamicTabComponent);
    }
  }


}
